<template>
  <div :id="formId" :class="[formAddBg, padAll, formStyle, animateForm]">
    <div class="top d-flex align-start mb-8">
      <div class="left text-left">
        <span
          :class="
            $vuetify.breakpoint.mdAndUp
              ? 'font-weight-bold headline'
              : 'font-weight:medium title'
          "
          >Add New Task</span
        >
        <br />
        <span
          class="font-weight-light body-1"
          style="fontFamily: 'Roboto Condensed', sans-serif !important"
          >and be nice person</span
        >
      </div>
      <v-spacer></v-spacer>
      <v-btn v-show="$vuetify.breakpoint.smAndDown" icon @click="closeForm">
        <v-icon>close</v-icon>
      </v-btn>
    </div>
    <div class="bottom">
      <form @submit.prevent="addNewEvent">
        <v-textarea
          v-model="task"
          prepend-inner-icon="assignment"
          :color="$vuetify.theme.dark ? 'accent' : 'secondary'"
          rows="2"
          placeholder="Task Name"
          required
        ></v-textarea>
        <v-menu
          ref="menuTwo"
          v-model="menu2"
          :close-on-content-click="false"
          :return-value.sync="time"
          transition="slide-x-transition"
          offset-y
        >
          <template v-slot:activator="{ on }">
            <v-text-field
              color="accent"
              v-model="time"
              placeholder="Time"
              required
              prepend-inner-icon="access_time"
              readonly
              v-on="on"
            ></v-text-field>
          </template>
          <v-time-picker
            v-if="menu2"
            color="accent"
            v-model="time"
            full-width=""
            @click:minute="$refs.menuTwo.save(time)"
          ></v-time-picker>
        </v-menu>
        <v-menu
          ref="menu"
          :nudge-top="100"
          v-model="menu"
          :close-on-content-click="false"
          transition="slide-x-transition"
          offset-y
        >
          <template v-slot:activator="{ on }">
            <v-text-field
              v-model="dateFormat"
              color="accent"
              placeholder="Date"
              persistent-hint
              readonly
              required
              prepend-inner-icon="event"
              v-on="on"
            ></v-text-field>
          </template>
          <v-date-picker
            v-model="date"
            color="accent"
            min="2018-01-01"
            full-width
            @change="menu = false"
          ></v-date-picker>
        </v-menu>
        <v-row class="mt-8 mx-0">
          <v-col cols="6" class="pa-0">
            <v-btn class="mr-1" block depressed color="warning" type="submit"
              >Save</v-btn
            >
          </v-col>
          <v-col cols="6" class="pa-0" @click="resetForm">
            <v-btn class="ml-1" block text>Reset</v-btn>
          </v-col>
        </v-row>
      </form>
    </div>
  </div>
</template>

<script>
import moment from 'moment';

export default {
  name: 'FormAdd',
  components: {
    // VRow: () => import('vuetify/lib'),
    // VCol: () => import('vuetify/lib'),
    // VIcon: () => import('vuetify/lib'),
    // VBtn: () => import('vuetify/lib'),
    // VMenu: () => import('vuetify/lib'),
    // VDatePicker: () => import('vuetify/lib'),
    // VTimePicker: () => import('vuetify/lib'),
    // VTextField: () => import('vuetify/lib'),
    // VTextarea: () => import('vuetify/lib')
  },
  props: {
    formStyle: String,
    padAll: String,
    formId: String,
    animateForm: String
  },
  data: () => ({
    task: '',
    date: null,
    time: null,
    menu: false,
    menu2: false
  }),
  computed: {
    dateFormat() {
      let date = val => {
        if (val === null) {
          return '';
        } else {
          return moment(val).format('dddd, DD MMMM YYYY');
        }
      };
      return date(this.date);
    },
    formAddBg() {
      return this.$vuetify.theme.dark ? 'dark-bg' : 'light-bg';
    }
  },
  methods: {
    closeForm() {
      this.$emit('formAddClosed');
    },
    async addNewEvent() {
      let event = {
        task: this.task,
        timestamp: `${this.date + ' ' + this.time}`,
        done: false,
        bg: Math.round(Math.random() * 2)
      };
      if (event.task.length > 0 && event.timestamp.length > 9) {
        this.$emit('setAlert', 'success');
        this.$emit('addNewEvent', event);
        await new Promise(resolve => setTimeout(resolve, 2000));
        this.closeForm();
      } else {
        this.$emit('setAlert', 'warning');
      }
    },
    resetForm() {
      this.task = '';
      this.time = null;
      this.date = null;
    }
  }
};
</script>
<style lang="scss">
.v-text-field {
  padding-top: 0 !important;
}
.formStyleMb {
  position: absolute;
  z-index: 11;
  left: 0;
  bottom: 0;
  right: 0;
  border-top-left-radius: 30px;
  border-top-right-radius: 30px;
}
.formStyleTb {
  position: sticky;
  top: 105px;
  z-index: 2;
}
.dark-bg {
  background-color: #102d37;
}
.light-bg {
  background-color: #f3f8f3;
}
</style>
